import React from "react";
import {Components} from "react-markdown";
import Typography from "@mui/material/Typography";


const H: Components["h1"] = (props) => {
    const {
        node,
        children
    } = props;

    const mapVariants: any = {
        "h1": "h1",
        "h2": "h2",
        "h3": "h3",
        "h4": "h4",
        "h5": "h5",
        "h6": "body1"
    };

    return (
        <>
            <Typography
              sx={{mb: 3}}
              variant={(node && mapVariants[node.tagName]) || "body1"}>
                {children}
            </Typography>

            {/*<pre>{JSON.stringify(rest, null, 4)}</pre>*/}
        </>
    );
};


export {H};
