import React, {Suspense} from "react";
import {BrowserRouter, Routes, Route, Outlet} from "react-router-dom";

import {PUBLIC_PATH, ROUTES} from "./env";
import {LoadingScreen, MarkdownScreen} from "./blocks";
import {HomePage} from "./pages";
import {DashboardLayout} from "./layouts";
import {ThemeProvider} from "./providers";
import "./App.scss";


const App: React.FC = () => {
    return (
        <ThemeProvider>
            <Suspense fallback={<LoadingScreen />}>
                <BrowserRouter basename={PUBLIC_PATH}>
                    <Routes>
                        <Route
                          element={
                            <DashboardLayout>
                                <Outlet />
                            </DashboardLayout>
                          }>
                            <Route path={ROUTES.home} element={<HomePage />} />
                            <Route path={ROUTES.installation} element={<MarkdownScreen path="installation.md" />} />
                            <Route path={ROUTES.overview} element={<MarkdownScreen path="overview.md" />} />
                            <Route path={ROUTES.pluginsProxy} element={<MarkdownScreen path="plugins/proxy.md" />} />
                            <Route path={ROUTES.pluginsMariadb} element={<MarkdownScreen path="plugins/mariadb.md" />} />
                        </Route>
                    </Routes>
                </BrowserRouter>
            </Suspense>
        </ThemeProvider>
    );
};


export {App};
