import React from "react";
import {Components} from "react-markdown";
import {Alert} from "@mui/material";


const Blockquote: Components["blockquote"] = (props) => {
    const {
        children
    } = props;

    return (
        <Alert
          icon={false}
          severity="info">
            {children}
        </Alert>
    );
};


export {Blockquote};
