import React, {useState, useCallback} from "react";
import {Link, useLocation} from "react-router-dom";
import {
    List,
    Typography,
    Collapse
} from "@mui/material";
import ArrowRightIcon from "@mui/icons-material/KeyboardArrowRightRounded";

import {MENU} from "../../../../env";


const Sidebar: React.FC = () => {
    const location = useLocation();
    const [openIndexes, setOpenIndexes] = useState<number[]>([]);

    const handleToggle = useCallback((index: number) => {
        setOpenIndexes((openIndexes) => {
            if(openIndexes.includes(index)) {
                return openIndexes.filter((openIndex: number) => {
                    return openIndex !== index;
                });
            }

            return [
                ...openIndexes,
                index
            ]
        });
    }, []);

    return (
        <List>
            {MENU.map((menuItem, index) => {
                let isActive = location.pathname === menuItem.to || (location.pathname.startsWith(menuItem.to) && location.pathname.charAt(menuItem.to.length) === "/");

                return (
                    <li key={index}>
                        <Typography
                          sx={{
                            display: "flex",
                            alignItems: "center",
                            ...isActive ? {
                                fontWeight: "bold"
                            } : {}
                          }}
                          component={Link}
                          variant="body1"
                          to={menuItem.to}
                          onClick={(e) => {
                            if(menuItem.children && menuItem.children.length > 0) {
                                e.preventDefault();
                            }

                            handleToggle(index);
                          }}>
                            {menuItem.children && menuItem.children.length > 0 && (
                                <ArrowRightIcon
                                  sx={{
                                    transition: "0.3s",
                                    transform: openIndexes.includes(index) ? "rotate(90deg)" : "rotate(0)"
                                  }}
                                  color="primary" />
                            )}

                            {menuItem.label}
                        </Typography>

                        {menuItem.children && (
                            <Collapse in={openIndexes.includes(index)}>
                                <List
                                  sx={{
                                    padding: 0,
                                    paddingLeft: 4
                                  }}>
                                    {menuItem.children.map((menuItem, index) => {
                                        let isActive = location.pathname === menuItem.to || (location.pathname.startsWith(menuItem.to) && location.pathname.charAt(menuItem.to.length) === "/");

                                        return (
                                            <li key={index}>
                                                <Typography
                                                  sx={{
                                                    ...isActive ? {
                                                        fontWeight: "bold"
                                                    } : {}
                                                  }}
                                                  component={Link}
                                                  variant="body1"
                                                  to={menuItem.to}>
                                                    {menuItem.label}
                                                </Typography>
                                            </li>
                                        );
                                    })}
                                </List>
                            </Collapse>
                        )}
                    </li>
                );
            })}
        </List>
    );
};


export {Sidebar};
